@keyframes filp{
    from{
        transform: translateY(0);
    }
    to{
        transform: translateY(-100%);
    }
}

@-webkit-keyframes filp{
    from{
        -webkit-transform: translateY(0);
    }
    to{
        -webkit-transform: translateY(-100%);
    }
}

@-moz-keyframes filp{
    from{
        -moz-transform: translateY(0);
    }
    to{
        -moz-transform: translateY(-100%);
    }
}


@keyframes red{
    0%{ color: #6D6E71; }
    
    99%{color: #6D6E71;}

    100%{ color: #F00; }
}